.organizationalDevelopment {
  display: flex;
  justify-content: center;
  gap: 15px;
  flex-wrap: wrap;

  min-height: 100vh;
  padding: 30px 0 0 0;

  p {
    display: flex;
    gap: 15px;
    width: 100%;

    font-weight: 600;
    font-size: medium;
    white-space: pre-line;

    &.listItem {
      position: relative;
      align-items: center;
      margin: 0 0 0 60px;
    }

    &.listItem:before {
      content: '';
      position: absolute;
      height: 8px;
      width: 8px;
      left: -20px;
      border-radius: 50%;
      background: #ef7f1a;
    }
  }

  .normal {
    text-align: left;
  }
}